body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1{
  text-align: center;
}
.gallery {
  padding: 0% 2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.gallery-item {
  width: 45%;
  border: 1px solid #ddd;
  border-radius: 5px;
  overflow: hidden;

  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.gallery-item img {
  width: 100%;
  height: auto;
}

.description {
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
  color: #333;
}

.learn-more {
  display: inline-block;
  margin: 10px 0;
  padding: 8px 16px;
  color: #fff;
  background-color: #007bff;
  text-decoration: none;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.learn-more:hover {
  background-color: #0056b3;
}




form{
  padding: 2% 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
form div{
  display: flex;
  flex-direction: row;
  justify-content: center;
}
form>*{
  width: 60%;
}
input,button {
  font-size: 20px;
  padding: 10px;
  border-radius: 5px;
}
input {
  display: block;
  margin-bottom: 20px;
  width: 35.5%;
}
button {
  border: 1px solid gray;
  background: whitesmoke;
  margin-right: 5px;
}
button:nth-last-child(2),
button:nth-last-child(1) {
  background: black;
  color: white;
}
form p{
  width: auto;
  background-color: black;
  padding: 15px 20px;
  color: white;
  border: 1px solid white;
  border-radius: 5px;
}


.intro-section {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 50px;
  height: 70vh;
}

.intro-text {
  max-width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.intro-text h1 {
  font-size: 2.5em;
  margin: 0;
}

.intro-text .highlight {
  color:#6265fb; /* Highlight color */
}

.intro-text p {
  font-size: 1.2em;
  margin: 10px 0 20px;
}

.about-btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.about-btn:hover {
  background-color: #0056b3;
}

.social-icons {
  margin-top: 20px;
}

.social-icons a {
  margin-right: 10px;
  display: inline-block;
}

.social-icons img {
  width: 70px;
  height: 70px;
  transition: transform 0.3s ease;
}

.social-icons img:hover {
  transform: scale(1.1);
}

.intro-image {
  max-width: 40%;
  display: flex;
  justify-content: end;
}

.intro-image img {
  width: 50%;
  border-radius: 50%;
}
@media only screen and (max-width:600px) {
  .intro-section{
    flex-direction: column;
  }
  .intro-image img{
    width: 100%;
  }
  .social-icons img {
    width: 50px;
    height: 50px;
    transition: transform 0.3s ease;
  }
  .gallery-item{
    width: 100%;
  }
}