
header,footer,body{
  background: rgb(79,235,231);
background: linear-gradient(90deg, rgba(79,235,231,1) 0%, rgba(90,172,241,1) 50%, #6265fb 100%); 
}
.App {
  text-align: center;
}

.App-logo {
  height: 100px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header{
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid rgba(238, 233, 233, 0.322);
}
.nav{
  display: flex;
  justify-content: center;
  padding: 1rem 1rem;
}
.nav-item{
  text-decoration: none;
  color: white;
  padding: 10px 15px;
  margin: 10px 10px;
  border-radius: 10px;
  align-self: center;
  
}
.nav-item:hover{
  background-color:#6265fb;
}
nav a.active{
  background-color: #6265fb;
}
